
import { _decorator, Component, Node, Prefab, instantiate, EventMouse, Touch, Vec2, Vec3, Button, math } from 'cc';
import { Card } from './Card';

import { CARDS_ARRAY } from './GameConstant';
import { GameManager } from './GameManager';
import ReaveldCard from './ReaveldCard';
const { ccclass, property } = _decorator;

@ccclass('GameScreen')
export class GameScreen extends Component {
    @property(Prefab)
    cardPrefeb: Prefab;
    @property(Node)
    reaveldCard: Node;
    @property(Button)
    deck: (Button);
    @property(GameManager)
    game: GameManager;
    @property(Node)
    mainArea: Node
    @property(Node)
    stack1: Node
    @property(Node)
    stack2: Node
    @property(Node)
    stack3: Node
    @property(Node)
    stack4: Node
    @property(Node)
    stack5: Node
    @property(Node)
    stack6: Node
    @property(Node)
    stack7: Node



    arrayOfCard: any[] = [];
    arrayOfCard2: any[] = [];
    h: any


    start() {
        this.arrayOfCard2 = CARDS_ARRAY;
        this.Shuffel(this.arrayOfCard2);
        this.defaultPlayArea();
    }
    OnClick() {
        this.creatCard();

        if (this.h == 52) {
            this.deck.interactable = false;

        }
        // this.getComponent(Card).init();
    }


    creatCard() {
        //console.log("dhoom dhoom");
        // for (let i = 0; i < 1; i++) {
        let card = instantiate(this.cardPrefeb);
        this.reaveldCard.addChild(card);
        card.getComponent(ReaveldCard).init(this.arrayOfCard2[0]);
        this.arrayOfCard2.splice(0, 1);
        this.h = this.arrayOfCard.push(card);
        console.log("Array", this.h);
    }

    Shuffel(data) {
        let currIndex = data.length, randomInadex;
        while (currIndex != 0) {
            randomInadex = Math.floor(math.random() * currIndex);
            currIndex--;
            [data[currIndex], data[randomInadex]] = [data[randomInadex], data[currIndex]];
        }
        return data;
    }
    stacklogic(stack) {
        for (let i = 0; i < 7; i++) {
            let card1 = instantiate(this.cardPrefeb);
            card1.getComponent(ReaveldCard).init(this.arrayOfCard2[0]);
            this.arrayOfCard2.splice(0, 1);
            stack.addChild(card1);
        }
    }

    defaultPlayArea() {
        let stack:number
        switch (stack) {
            case 1:
                this.stacklogic(this.stack1)
                break;
            case 2:
                this.stacklogic(this.stack2);
                break;
            case 3:
                this.stacklogic(this.stack3);
                break;
            case 4:
                this.stacklogic(this.stack4);
                break;
            case 5:
                this.stacklogic(this.stack5);
                break;
            case 6:
                this.stacklogic(this.stack6);
                break;
            case 7:
                this.stacklogic(this.stack7);
                break;
        }
    }
}
